import React ,{ Component } from 'react';
import Link from 'next/link';
import Image from '../../UI/Image/Image';
import Button from '../../UI/Button/Button';
import Title from '../../UI/Title/Title';
import Router from 'next/router';

class Post extends Component{
    constructor(props){
        super(props);        
        this.addFavoriteItem = this.addFavoriteItem.bind(this);

    }

    async addFavoriteItem() {
        console.log("called" ,this.props.name);
        const res=  await fetch('http://localhost:8080/add?name='+this.props.name)
        const json = await res.json()
        return(json);
      }
    render(){
        
        return(
            <div className="row">
            <Title username={this.props.name}/>
            <Image image={this.props.image}/>     
            <a onClick={()=>this.addFavoriteItem()}><Button>fa fa-fw fa-heart</Button></a>
        
            <style jsx>
            {`
                img {
                    max-width: 100%;
                    height: auto;
                }
                a{
                    position: absolute;
                    
                }
            
            `}    
            </style>
            <hr/>
    </div>
    );
}}



export default Post;